import React, { ReactNode } from "react"
import { Box, Typography } from "@rp/react/web/mui/core"
import { ClosePaneButton } from "../ClosePaneButton"

type FilesDashPaneProps = {
  children: ReactNode
  header?: string | ReactNode
  minWidth: string
  width: string | null
  close?: () => void
}

function FilesDashPane({ children, width, close, header, minWidth }: FilesDashPaneProps) {
  return (
    <Box
      sx={theme => ({
        backgroundColor: "elevation.dp01",
        border: `1px solid ${theme.palette.grey.A700}`,
        borderRadius: "16px",
        boxShadow: theme.shadows[24],
        display: "block",
        height: "calc(100vh - 80px)",
        m: 1,
        overflow: "hidden",
        position: "sticky",
        top: "70px",
        transition: "0.2s ease-in-out",
        visibility: width ? "visible" : "hidden",
        width: width || 0,
        minWidth,
        zIndex: 30,
      })}
    >
      <Box sx={{ display: "flex", alignItems: "center", justifyContent: "space-between" }}>
        {typeof header === "string" ? (
          <Typography fontWeight={700} fontSize="20px">
            {header}
          </Typography>
        ) : (
          header
        )}
      </Box>

      {close && (
        <Box sx={{ display: "flex", justifyContent: "flex-end", m: 1 }}>
          <ClosePaneButton close={close} />
        </Box>
      )}

      <Box>{children}</Box>
    </Box>
  )
}

export { FilesDashPane }
